  width: calc(25% - 18px);
  min-height: 25vh;
  transition: transform .4s ease-in-out;
}

.info {
  padding: 0 12px;
}

.load-more {
  width: 126px;
  height: 36px;
  margin: 0 auto;
  display: block;
}

.photo-card:hover {
  transform: scale(1.05);
  box-shadow: 6px 4px 6px #b2ffff, 2px 4px 8px #b2ffff, 3px 5px 11px #e7ffff;
}

